var autoSizeText;

autoSizeText = function() {
  var el, elements, _i, _len, _results;
  elements = $('.resize:visible');
  console.log(elements);
  if (elements.length < 0) {
    return;
  }
  _results = [];
  for (_i = 0, _len = elements.length; _i < _len; _i++) {
    el = elements[_i];
    _results.push((function(el) {
      var resizeText, _results1;
      resizeText = function() {
        var elNewFontSize;
        if($(elements).prop("tagName") == 'H2'){
            elNewFontSize = (parseInt($(el).css('font-size').slice(0, -2)) - 0.5) + 'px';
        } else{
            elNewFontSize = (parseInt($(el).css('font-size').slice(0, -2)) / 2) + 'px';
        }
        return $(el).css('font-size', elNewFontSize);
      };
      _results1 = [];
      while (el.scrollHeight > el.offsetHeight) {
        _results1.push(resizeText());
      }
       return _results1;
    })(el));
  }
  return _results;
};


$(document).ready(function(){
    
    if($(document).width() <= 768){
        $('html').width($(document).width());
    }
    
    $('[data-toggle="tooltip"]').tooltip();
    
    if($('.adviser-name h2').length > 0)
    {
        
        setInterval(function(){
            autoSizeText();
        }, 700);
        
    }
    
    $('.tooltip-item').tooltip();
    
    $('.popover-item').popover({'trigger': 'hover', 'placement': 'top'});
    
    $('.popover-item.visible').popover('show');
    
    $('.hometop-banner').css({'height':$(window).height()});
    
    $('.contact-item > center, .contact-area > .tab-content > .tab-pane').height($(window).height());
    
    $('.contact-item > img').height($('.contact-item center').height());
    
    $('.modal.open-on-startup').modal('show');

    $(document).on({
        click: function(e){
            e.preventDefault();
            var target = $(this).attr('href');
            $('.skillitem, .skill-details').hide();
            $(target).show();
        }
    }, '.skillset');    

    $(document).on({
        click: function(e){
            e.preventDefault();
            var target = $(this).attr('href');
            $('.skill-details').hide();
            $('[skill-details='+target+']').show();
        }
    }, '.toggle-skill-details');    

    
    $(window).scroll(function(e){
        if($('.company-graphs').length > 0){
        var w_bottom   = $(window).scrollTop() + $(window).height();
        var g_bottom   = $('.company-graphs').position().top + $('.company-graphs').height();
        
        var w_top      = $(window).scrollTop();
        var g_top      = $('.company-graphs').position().top;
        
        if(w_bottom < g_bottom+$(window).height() && w_bottom > g_bottom-$(window).height() )
        {
            
            if($('.graph-column-2:eq(0)').height() == 0){
                
                $('.graph-column-2:eq(0)').animate({
                    'height': '270px'
                },3000);
                
                $('.graph-column-2:eq(1)').animate({
                    'height': '225px'
                },2500);
                
                $('.graph-column-2:eq(2)').animate({
                    'height': '350px'
                },6000);
                
                $('.graph-column-2:eq(3)').animate({
                    'height': '300px'
                },4000);
                
            }
            
        } else {
            
            if($('.graph-column-2:eq(0)').height() == 270) {
            
                $('.graph-column-2').animate({'height': '0px'},500);
                
            }
            
        }
        
        }
    });
    
    if($('.slick').length > 0){
        $('.slick').each(function(){
            var slick = $(this).slick(JSON.parse($(this).attr('data-slick'))).on('afterChange', function(){ progressBar($(this)) });
        
            progressBar($(this));
        });
    }
    
    if(window.location.hash.length > 0){
        $('html, body').animate({
            scrollTop: $(window.location.hash).offset().top
        }, 100);
    }
    
    
});


$(window).load(function(){
    
    $("[source]").each(function(i){ $(this).attr({'src': $(this).attr('source')}).removeAttr('source') });
    $("[data-source]").each(function(i){ $(this).attr({'src': $(this).attr('data-source')}).removeAttr('data-source') });
    
    
    $('[second-source]').each(function(){
        var subject = $(this);
        setTimeout(function(){
            
            subject.load(subject.attr('second-source'), function(){
                
                subject.removeAttr('second-source');
                subject.removeAttr('second-delay');
                
                subject.find('[data-toggle="tooltip"]').tooltip();
    
                subject.find('.tooltip-item').tooltip();
                
                subject.find('.popover-item').popover({'trigger': 'hover', 'placement': 'top'});
                
                subject.find('.popover-item.visible').popover('show');
                
                $("[source]").each(function(i){ $(this).attr({'src': $(this).attr('source')}).removeAttr('source') });
                $(this).find("[data-source]").each(function(i){ $(this).attr({'src': $(this).attr('data-source')}).removeAttr('data-source') });
                
                subject.find('.slick').each(function(){
                    var slick = $(this).slick(JSON.parse($(this).attr('data-slick'))).on('afterChange', function(){ progressBar($(this)) });
                    
                    progressBar($(this));
                });
                
                /*=== Owl gallery ====*/
            	subject.find(".enable-owl-carousel").each(function(i) {
            					
            		var $owl = $(this);
            						
            		var navigationData = $owl.data('navigation');
            		var paginationData = $owl.data('pagination');
            		var singleItemData = $owl.data('single-item');
            		var autoPlayData = $owl.data('auto-play');
            		var transitionStyleData = $owl.data('transition-style');
            		var mainSliderData = $owl.data('main-text-animation');
            		var afterInitDelay = $owl.data('after-init-delay');
            		var stopOnHoverData = $owl.data('stop-on-hover');
            		var owlEffect		= $owl.data('effect');
            		var min600 = $owl.data('min600');
            		var min800 = $owl.data('min800');
            		var min1200 = $owl.data('min1200');
            						
            		$owl.owlCarousel({
            			navigation : navigationData,
            			pagination: paginationData,
            			singleItem : singleItemData,
            			autoPlay : autoPlayData,
            			transitionStyle : transitionStyleData,
            			stopOnHover: stopOnHoverData,
            			navigationText : ["",""],
            			autoplayTimeout: 2000,
            			slideSpeed: 3000,
            			paginationSpeed: 3000,
            			rewindSpeed: 10000,
            			lazyLoad : true,
            		    lazyFollow : true,
            		    lazyEffect : owlEffect,
            			itemsCustom:[
            				[0, 1],
            				[600, min600],
            				[800, min800],
            				[1200, min1200]
            			]
            		});
            	});
            	
            	if(window.location.hash.length > 0){
                    $('html, body').animate({
                        scrollTop: $(window.location.hash).offset().top
                    }, 100);
                }
              
            });
            
        }, subject.attr('second-delay')*1 );
    });

});